
import XHeader from 'vux/src/components/x-header/index.vue'

import { mapGetters } from "vuex";
export default {
components: {
XHeader
},
computed: {
...mapGetters(["lessonDetail"])
},
created() {



},
activated() {
this.$store.dispatch("getLessonDetail", {
id: this.$route.params.lessonid
});
},
deactivated() {

},
watch: {
lessonDetail(Value) {
this.infos = Value;
},
infos: function(val) {
let htmlUtil = this.$options.filters.htmlUtil;
this.realHtml = htmlUtil(val.thumb_content);
}
},
data() {
return {
infos: {},

realHtml: ""
};
},
filters: {
htmlUtil: function(str) {
let s = "";
if (str.length == 0) return "";
s = str.replace(/&amp;/g, "&");
s = s.replace(/&lt;/g, "<");
s = s.replace(/&gt;/g, ">");
s = s.replace(/&nbsp;/g, " ");
s = s.replace(/&#39;/g, "'");
s = s.replace(/&quot;/g, '"');
return s;
}
}
};



